.header {
    position: fixed;
    display: flex;
    flex-direction: column;
    width: 72px;
    background-color: cornsilk;
    height: 100%;
    align-items: center;
   justify-content: center;
   z-index: 4;
}
.LinkHeader {
    max-height: 80px;
    padding: 20px 0;
    cursor: default;
}
.LinkHeader img {
    cursor: pointer;
    height: 60px;
    width: 60px;
    transition:  all .3s;
}
/* .LinkHeader .active {
    color: brown;
} */
.LinkHeader img:hover{
    height: 75px;
    width: 75px;
    transition: all .3s;
}
