/* ============= */
.global_cont {
    /* position: absolute; */
    display: flex;
    justify-content: center;
    margin-left: 77px;
}
.container {
    transform-origin: 0 50%;
    margin: 0 1em;
    border-radius: 4px;
    overflow: hidden;
    background-color: #fff;
    transition-property: opacity, transform;
    width: 400px;
    height: 400px;
  }
  
  .content {
   width: 100%;
    height: 100%;
  }
  .img {
    height: 100%;
    width: 100%;
  }
  
  .footer {
    color: #333;
    font-weight: 700;
    font-size: 1.25em;
    text-align: center;
    padding: 1em;
  }

  .swiper-wrapper {
      display: flex;
  }

  .Slide {
    margin: 0 20px;
}



  @media screen and (max-width: 1200px) {
    .container {
        width: 300px;
        height: 300px;
    }
  }
  @media screen and (max-width: 906px) {
    .container {
        width: 34vw;
        /* height: 300px; */
        height: 40vh;
    }
  }
  @media screen and (max-width: 692px) {
    .container {
        width: 54vw;
        height: 40vh;

    }
  }

  