.iteMessMy{
    margin: 20px 0;
}


.inputText{
    width: 65vw;
    height: 30px;
}
.button {
    height: 30px;
    width: 7vw;
}

.sendMessage{
    width: 72vw;
}

.button.false_b{
    height: 30px;
    width: 7vw;
    background-color: white;
    color: white;
}
.anotherUser{
    margin: 20px 0;
    margin-left: auto;

   
}
.allthemessage {
    display: flex;
    flex-direction: column; 
}
.MainMessageDialogs{
  margin-bottom: 20px; 
}

@media screen and (max-width: 692px) {
    .MainMessageDialogs{
        font-size: 17px;
    }
       .sendMessage{
           position: absolute;
        margin: 0 0 0 -180px;
           bottom: 0;
           display: flex;
           z-index: 5;
           min-width: 100vw;

       }
       .inputText{
        width: 100vw;
        height: 50px;
    }
    .button {
        height: 50px;
        width: 10vw;
    }
    .button.false_b{
        height: 50px;
        width: 12vw;
        background-color: white;
        color: white;
    }
    }
    @media screen and (max-width: 400px) {
        .inputText{
            width: 88vw;
            height: 50px;
        }
        .button {
            height: 50px;
            width: 12vw;
        }
        
    }
